.anchorDetailContainer {
  width: 100%;
  height: 100%;
  overflow: scroll;
  background: url("../images/detailBg.svg") center top / 100% no-repeat;

  .anchorDetailHeader {
    width: 100%;
    display: flex;

    .anchorDetailHeaderContent {
      height: 100%;
      background-color: rgba(0, 0, 0, 0);
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      justify-content: center;

      .nick {
        color: black;
        font-weight: bold;
        text-align: center;
      }

      .desc {
        color: rgb(133, 133, 133);
        text-align: center;
      }
    }
  }

  .line {
    height: 3px;
    background: linear-gradient(45deg, rgb(237, 237, 237), rgb(189, 189, 189));
  }

  .imageContent {
    background: #fff;
    text-align: center;
    padding: 20px 0;

    img {
      height: 100%;
    }
  }

  .profileContainer {
    background: white;

    .profileHeader {
      .title {
        display: flex;
        align-items: center;
        overflow: hidden;

        .circle {
          border-radius: 50%;
          background-color: black;
          display: flex;
          justify-content: center;
          align-items: center;

          .inner {
            border-radius: 50%;
            background-color: white;
          }
        }

        p {
          font-weight: bold;
          color: black;
        }
      }

      .desc {
        color: rgb(45, 45, 45);
        padding: 0 20px;
        margin-top: 30px;
      }
    }
  }
}