.anchorListContainer{
  width: 100%;
  height: 100%;
  background: url("../images/bg.svg") center top / 100% no-repeat;
  overflow: scroll;
  .searchBox{
    background: white;
    margin: 24px auto;
    position: relative;
    input{
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      box-sizing: border-box;
      padding-left: 12px;
    }
    button{
      height: 100%;
      position: absolute;
      right: 0;
      top: 0;
      border: none;
    }
  }
  .picList{
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    .every{
      .pic {
        background: white;
        overflow: hidden;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .anchorName{
        text-align: center;
        margin: 15px 0 30px 0;
      }
    }
  }
}