.menu{
  width: 100%;
  height: 100%;
  background: linear-gradient(115deg, rgb(0, 211, 135), rgba(97, 220, 183, 0) 33%), linear-gradient(-48deg, rgb(251, 226, 0) 1%, rgba(255, 143, 81, 0.6) 16%, rgba(255, 143, 81, 0) 27%), linear-gradient(117deg, rgb(0, 101, 177) 40%, rgba(0, 117, 255, 0) 105%), linear-gradient(75deg, rgb(98, 0, 223) 18%, rgba(0, 56, 255, 0) 87%), linear-gradient(-70deg, rgb(255, 0, 138) 50%, rgba(255, 0, 46, 0) 85%);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10000;
  color: white;
  font-weight: 600;
  padding-left: 50px;
  overflow: hidden;
  .content{
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    div{
      margin-top: 20px;
    }
  }
  button{
    position: absolute;
    top: 15px;
    right: 15px;
  }
}

.header{
  width: 100%;
  background: white;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  button{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background: white;
    border: none;
    span{
      width: 15px;
      height: 3px;
      background: black;
      display: inline-block;
    }
  }
}